	module.exports = {"data":{"post":{"html":"<p><a title=\"Adam Curry's Weblog\" href=\"http://www.blognewsnetwork.com/members/0000001/2003/10/09.html#a4571\">Adam Curry:</a> \"I'm jealous of the vigor and dedication that goes into blogging political and social issues in the states. Believe me that there is no country in Europe as adamant about certain inalienable rights, like free speech. When it comes to that Dave [Winer] is right, blogging truly is an 'american thing'....</p>\n<p>\"We're on the eve of the birth of a constitution for a new union of states (Europe), comprised of 400 million people with a single currency. Surely that is worth blogging about. Unfortunately the blogosphere in my neck of the woods hasn't quite moved beyond the stage of commenting on traditional media. In turn, Big Media here is still reporting that weblogs are only used by teenage girls who write about their pets. Sound familiar?</p>\n<p>\"I feel as if it's my duty as an american to blog the creation of the european constitution, get people interested in the process and (hopefully) the outcome. There's ample opportunity to shape the ground rules and laws that the citizens will abide to.\"</p>\n<p>The European blogs I read seem to blog more about US politics than their own politics. We should be talking about Berlusconi, not Schwarzenegger.</p>","fields":{"title":"Silvio, not Arnie","date":"09 Oct 2003","url":"https://papascott.de/2003-10-09-silvio-not-arnie/"}}},"pathContext":{"slug":"/2003-10-09-silvio-not-arnie/","prev":{"fields":{"title":"Is that Ellen Feiss advertising for T-Online?","date":"09 Oct 2003","slug":"/2003-10-09-is-that-ellen-feiss-advertising-for-t-online/"}},"next":{"fields":{"title":"CDU has problem with health","date":"10 Oct 2003","slug":"/2003-10-10-cdu-has-problem-with-health/"}}}}

